<template>
  <div id="container">
    <h1>{{title}}</h1>
    <div id="main" @mouseover="stop" @mouseout="move">
      <ul id="carousel" :style="carouselStyle" :class="{carouselLeft:carouselData.position==0,carouselRight:carouselData.position==1,carouselTop:carouselData.position==2,carouselBottom:carouselData.position==3}">
        <li v-for="(pic,index) in carouselData.src"><img :src="pic.src"/></li>
      </ul>
      <ul :class="{navListRow:carouselData.position==0||carouselData.position==1,navListCol:carouselData.position==2||carouselData.position==3}">
        <li v-for="n in 4" class="navLi" @click="choose(n)" :class="{selected:carouselNum==n}"></li>
      </ul>
    </div>
    <div style="border: 1px solid #999;margin: 40px 150px;">
      <div id="directionBtn">
        <label v-for="(directions,index) in directionBtn"><input type="radio" name="direction" :value="index" v-model="directionNum">{{directions}}</label>
      </div>
      <div style="margin: 20px 0;">
        <label>轮播速度<input type="text" v-model="carouselData.interval" @blur="speed" style="width: 50px">(毫秒)</label>
      </div>
      <div style="margin-bottom: 50px;">
        <input type="button" value="修改" style="width: 70px;height: 40px;" @click="submit">
      </div>
    </div>
  </div>
</template>

<script>
  export default{
      name:'carousel',
      data(){
          return {
              title:'轮播图',
              carouselData:{
                  src:[{src:require('../../assets/carousel1.jpg')},{src:require('../../assets/carousel2.jpg')},{src:require('../../assets/carousel3.jpg')},{src:require('../../assets/carousel4.jpg')},{src:require('../../assets/carousel1.jpg')}],
                  interval:2000,
                  position:0,
              },
              directionBtn:['向左','向右','向上','向下'],
              directionNum:0,
              carouselNum:1,
              carouselStyle:null,
              timer:null,
              width:600,
              height:300,
          }
      },
      methods:{
          carLeft(){
            switch (this.carouselNum) {
              case 1:
                this.carouselStyle='left: -'+this.width+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 2:
                this.carouselStyle='left: -'+this.width*2+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 3:
                this.carouselStyle='left: -'+this.width*3+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 4:
                this.carouselStyle='left: -'+this.width*4+'px;transition: all 1s ease-in-out;';
                setTimeout(()=>{
                  this.carouselStyle='left: 0;';
                  this.carouselNum=1;
                },1000);
                break;
            }
          },
          carRight(){
            switch (this.carouselNum) {
              case 4:
                this.carouselStyle='left: -'+this.width*3+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 3:
                this.carouselStyle='left: -'+this.width*2+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 2:
                this.carouselStyle='left: -'+this.width*1+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 1:
                this.carouselStyle='left: 0px;transition: all 1s ease-in-out;';
                setTimeout(()=>{
                  this.carouselStyle='left: -'+this.width*4+'px;';
                  this.carouselNum=4;
                },1000);
                break;
            }
          },
          carTop(){
            switch (this.carouselNum) {
              case 1:
                this.carouselStyle='top: -'+this.height+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 2:
                this.carouselStyle='top: -'+this.height*2+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 3:
                this.carouselStyle='top: -'+this.height*3+'px;transition: all 1s ease-in-out;';
                this.carouselNum++;
                break;
              case 4:
                this.carouselStyle='top: -'+this.height*4+'px;transition: all 1s ease-in-out;';
                setTimeout(()=>{
                  this.carouselStyle='top: 0px;';
                  this.carouselNum=1;
                },1000);
                break;
            }
          },
          carBottom(){
            switch (this.carouselNum) {
              case 4:
                this.carouselStyle='top: -'+this.height*3+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 3:
                this.carouselStyle='top: -'+this.height*2+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 2:
                this.carouselStyle='top: -'+this.height+'px;transition: all 1s ease-in-out;';
                this.carouselNum--;
                break;
              case 1:
                this.carouselStyle='top: 0px;transition: all 1s ease-in-out;';
                setTimeout(()=>{
                  this.carouselStyle='top: -'+this.height*4+'px;';
                  this.carouselNum=4;
                },1000);
                break;
            }
          },
          move(){
            switch (this.carouselData.position) {
              case 0:
                this.timer=setInterval(this.carLeft,this.carouselData.interval);
                break;
              case 1:
                this.timer=setInterval(this.carRight,this.carouselData.interval);
                break;
              case 2:
                this.timer=setInterval(this.carTop,this.carouselData.interval);
                break;
              case 3:
                this.timer=setInterval(this.carBottom,this.carouselData.interval);
                break;
            }
          },
          stop(){
            clearInterval(this.timer);
          },
          speed(){
            if(this.carouselData.interval<2000) {
              this.carouselData.interval=2000;
            }
          },
          submit(){
            this.stop();
            this.carouselData.position=this.directionNum;
            switch (this.carouselData.position) {
              case 0:
                this.carouselStyle='left: 0;';
                this.carouselNum=1;
                break;
              case 1:
                this.carouselStyle='left: -'+this.width*4+'px;';
                this.carouselNum=4;
                break;
              case 2:
                this.carouselStyle='top: 0px;';
                this.carouselNum=1;
                break;
              case 3:
                this.carouselStyle='top: -'+this.height*4+'px;';
                this.carouselNum=4;
                break;
            }
            this.move();
          },
          choose(n){
            switch (this.carouselData.position) {
              case 0:
                this.carouselStyle='left: -'+this.width*(n-1)+'px;transition: all 1s ease-in-out;';
                this.carouselNum=n;
                break;
              case 1:
                this.carouselStyle='left: -'+this.width*n+'px;transition: all 1s ease-in-out;';
                this.carouselNum=n;
                break;
              case 2:
                this.carouselStyle='top: -'+this.height*(n-1)+'px;transition: all 1s ease-in-out;';
                this.carouselNum=n;
                break;
              case 3:
                this.carouselStyle='top: -'+this.height*n+'px;transition: all 1s ease-in-out;';
                this.carouselNum=n;
                break;
            }
          },
      },
      created(){
          this.move();
      }
  }
</script>

<style scoped>
  #container {
   text-align: center;
   width: 800px;
   height: 700px;
  }
  h1 {
    margin-bottom: 30px;
  }
  #main {
    width: 600px;
    height: 300px;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
  }
  .navListRow {
    position: absolute;
    left: 50%;
    bottom: 20px;
    margin-left: -32px;
    width: 64px;
    height: 16px;
    background-color: rgba(255,255,255,.3);
    border-radius: 10px;
  }
  .navListCol {
    position: absolute;
    top: 50%;
    right: 20px;
    margin-top: -32px;
    height: 64px;
    width: 16px;
    background-color: rgba(255,255,255,.3);
    border-radius: 10px;
  }
  .navLi {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #fff;
    float: left;
    margin: 3px;
    cursor: pointer;
  }
  #main .selected {
    background-color: #ff5000;
  }
  .carouselLeft {
    position: absolute;
    top: 0;
    left: 0;
    width: 3000px;
    height: 300px;
  }
  .carouselLeft li {
    float: left;
  }
  .carouselRight {
    position: absolute;
    top: 0;
    left: -2400px;
    width: 3000px;
    height: 300px;
  }
  .carouselRight li {
    float: left;
  }
  #carousel img {
    width: 600px;
    height: 300px;
  }
  .carouselTop {
    position: absolute;
    top: 0;
    left: 0;
    width: 600px;
    height: 1500px;
    text-align: left;
  }
  .carouselTop li {
    height: 300px;
  }
  .carouselBottom {
    position: absolute;
    top: -800px;
    left: 0;
    width: 600px;
    height: 1500px;
    text-align: left;
  }
  .carouselBottom li {
    height: 300px;
  }
  #directionBtn {
    margin-top: 50px;
  }
</style>



